.search-booking {
  position: relative;

  min-height: 650px;

  @media (min-width: @desktop-width) {
    margin-bottom: 10px;
  }

  .search-booking iframe {
    position: relative;
    z-index: 5;

    @media (min-width: @tablet-width) {
      top: 10px;
    }
  }

  &__map {
    margin-top: 0;
  }

  &__map picture {
    content: "";
    position: absolute;
    top: 275px;
    left: 0;
    z-index: -1;

    @media (min-width: @tablet-width) {
      top: 263px;
    }

    @media (min-width: @desktop-width) {
      top: 274px;
    }
  }

  &__map picture::before {
    content: "";
    position: absolute;
    bottom: 170px;
    left: 45%;

    width: 27px;
    height: 27px;

    background-image: url("../img/icon-map-marker.svg");
    background-repeat: no-repeat;
    border-radius: 50%;

    @media (min-width: @tablet-width) {
      bottom: 165px;
      left: 48%;
    }

    @media (min-width: @desktop-width) {
      bottom: 165px;
      left: 48.5%;
    }
  }

  & h2 {
    margin-bottom: 0;

    font-size: @big-font-size;
    line-height: @big-line-height;

    @media (min-width: @tablet-width) {
      margin-bottom: 30px;

      font-size: @biggest-font-size;
      line-height: @main-line-height;
    }
  }

  &__trip-date {
    margin-bottom: 30px;
    margin-top: 10px;
    margin-left: 14px;
    margin-right: 14px;
    padding-left: 5px;
    padding-right: 5px;

    font-size: @small-font-size;
    line-height: @main-line-height;

    @media (min-width: @tablet-width) {
      margin-bottom: 42px;
      margin-left: 150px;
      margin-right: 150px;

      line-height: @medium-line-height;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 45px;
      margin-left: 370px;
      margin-right: 370px;
    }
  }

  &__trip-date span {
    @media (min-width: @tablet-width) {
      margin-left: 80px;
      margin-right: 75px;
    }
  }

  &__button {
    display: block;

    margin: 0 auto;
    margin-bottom: 55px;
    padding: 17px 47px;
    width: 280px;

    font-size: @big-font-size;
    line-height: @main-line-height;
    font-weight: bold;
    color: @another-text-color;
    background-color: @main-color;

    @media (min-width: @tablet-width) {
      margin-bottom: 55px;
      width: 458px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 65px;
    }
  }

  &__button:hover {
    background-color: #669ec0;
  }

  &__button:focus,
  &__button:active {
    color: rgb(148, 185, 210);
    background-color: #5496bd;
  }

  &__button[disabled] {
    background-color: #e5e5e5;
  }
}
